  --card-shadow: 0px 1px 1px rgba(0, 0, 0, .12), 0px 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p, ul {
